import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/component/Home.vue' 

import Client from '@/component/client.vue' 
 
import team from '@/component/testmonial.vue' 

import services from '@/component/services.vue' 

Vue.use(Router)

export default new Router({
    mode: 'hash',//  mode: 'history',||  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,      
      
    },
    {
      path: '/Client',
      name: 'Client',
      component: Client,      
      
    },
    {
      path: '/team',
      name: 'team',
      component: team,      
      
    },
    {
      path: '/services',
      name: 'services',
      component: services,      
      
    } 
  ]
})
